* {
	box-sizing: border-box;
}

body {
	font-family: $font-family--primary;
    font-size: $base-font--size;
    color: palette(grey, x-dark);
    line-height: $base__line / $base-font--size;
    background-color: $greenland--green;
}

h1 {
	font-size: em(52px);
	color: $greenland-green--dark;
	font-weight: $font-weight--large;
	margin-bottom: em(34px, 62px);
	letter-spacing: $letter-space--minus;
	text-align: center;
}

img {
	max-width: 100%;
}

a {
	color: $greenland--green;
	text-decoration: none;
}