
// Importing Google fonts

@if variable-exists(font-stack--primary ) {
	@import url($font-stack--primary);
}

// px to em 

@function em($target, $context: $base-font--size) {
	@return ($target / $context) * 1em;
}

// px t- percentage

@function px-to-pc($target, $context: $max-width) {
	@return ($target/$context) * 100%;
}

// Creat perline count

@function per-line($item) {
	$g-pct: px-to-pc($gutter) * 2;
	$g-total: $item * $g-pct;
	@return (100% / $item) - $g-total;
}

// Call the color palettes modifier

@function palette($palette, $shade: 'base') {
	@return map-get(map-get($palettes, $palette), $shade);
}