// Importing Google fonts

$font-stack--primary       : 'https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,600;0,800;1,300&display=swap';


// Font Family 

$font-family--primary      : 'Open Sans', sans-serif;

// Fonts Weight

$font-weight--light        : 300;
$font-weight--medium       : 600;
$font-weight--large        : 800;


// Colors

$greenland-green--light   : lighten(#22a6b3, 20%);
$greenland--green         : #22a6b3;
$greenland-green--dark    : darken(#22a6b3, 20%);

$queens-jelly--light      : lighten(#f0932b, 20%);
$queens--jelly            : #f0932b;
$queens-jelly--dark       : darken(#f0932b, 20%);

$carmine-pink--light      : lighten(#eb4d4b, 20%);
$carmine--pink            : #eb4d4b;
$carmine-pink--dark       : darken(#eb4d4b, 20%);

$black                    : #000;
$white                    : #fff;
$grey                     : #747d8c;

// Buttons Maps

$ui-colors: (
	default     : $greenland--green,
	success     : $queens--jelly,
	warning     : $carmine--pink,
	error       : $carmine-pink--dark,
	info        : $queens-jelly--dark
);

// Color Maps

$palettes: (
	grey: (
		xx-light  : lighten($grey, 65%),
		x-light   : lighten($grey, 45%),
		light     : lighten($grey, 32%),
		base      : $grey,
		dark      : darken($grey, 8%),
		x-dark    : darken($grey, 16%)
	),
	black: (
		xx-light  : lighten($black, 65%),
		x-light   : lighten($black, 45%),
		light     : lighten($black, 32%),
		base      : $black,
		dark      : darken($black, 8%),
		x-dark    : darken($black, 16%)
	)
);

// Units

$base-font--size          : 16px;
$base__line               : 24px;
$trans                    : 0.4s;
$br-radius                : 4px;
$br-round                 : 50%;
$letter-space             : 1px;
$letter-space--minus      : -2px;

//Breakpoints

$gutter                   : 20px;
$max-width                : 1140px;

$breakpoints: (
	'xs'  : 575px,
	'sm'  : 576px,
	'md'  : 768px,
	'lg'  : 998px
);