
.form {
	max-width: 350px;
	margin: 45px auto;
	background-color: palette(grey, x-light);
	border-radius: $br-radius;
	padding: 30px 40px;
}

label {
	font-size: em(15px);
	font-weight: $font-weight--large;
	color: palette(grey);
}

input[type="text"],
input[type="password"],
select {
	width: 100%;
	border: none;
	outline: none;
	padding: 8px 15px;
	font-size: em(20px);
	font-weight: $font-weight--medium;
	color: palette(grey);
	margin-bottom: em(24px, 20px);
	background-color: transparent;
	border-bottom: 2px solid palette(grey, light);
}

input:focus {
	border-bottom: 2px solid $greenland--green;
}

.avatar {
	display: block;
	margin: 0 auto em(50px);
	@include roundy(150px, 150px);
}

.reset-details {
	span {
		display: block;
		font-size: em(15px);
		margin-bottom: em(5px, 15px);
		.fa-dot-circle-o {
			color: $greenland--green;
		}
	}
}

// Registartion Form

.main {
	background-color: palette(grey, x-light);
	margin: 65px auto;
	max-width: 1000px;

	@include mq(sm) {
		@include flexy($wrap: wrap);
	}

	&-left {
		background: url('../img/crv8.jpg') no-repeat;
		background-size: cover;
		background-position: 0px 0px;
		@include mq(xs) {
			height: 300px;
		}
		@include mq(sm) {
			flex-basis: 50%;
		}

		article {
			padding: 20px;
			text-align: center;
			color: $white;
			margin-top: 150px;
			h2 {
				font-size: em(42px);
			}
		}
	}
	&-right {
		padding: 30px 40px;
		@include mq(sm) {
			flex: 1;
		}
	}
}
