
// Buttons

%btn {
	width: 100%;
	display: block;
	border: none;
	outline: none;
	font-size: em(20px);
	text-transform: uppercase;
	font-weight: $font-weight--medium;
	display: block;
	padding: 10px 25px;
	margin: em(30px, 20px) 0;
	color: $white;
	border-radius: $br-radius;
	cursor: pointer;
}